.bm-menu {
  /* background:linear-gradient(39.97deg, #03d6f3 0.79%, #458ff6 79.29%); */

  // background:linear-gradient(to left, rgb(244, 92, 67), rgb(234, 46, 68));

  
  background:$red;
  padding: 2.5em 1.5em 0;
  height: 100vh;
  overflow: hidden !important;
}






/* Position and sizing of burger button */

.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    left: 36px;
    top: 36px;
  }
  
  /* Color/shape of burger icon bars #373a47;*/
  .bm-burger-bars {
    background:$grey;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background:$red;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross #bdc3c7 */
  .bm-cross {
    background:$blue-grey;
  }
  

  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100vh;
    overflow-x: hidden;
    box-shadow: 0px,4px,31px rgba(0, 0, 0, 0.15);
  }
  
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
  }


  /* .bm-morph-shape {
    fill: #373a47;
  }
    */

  /* Wrapper for item list */

  .menu-item{
      position: relative;
      font-size: 2em;
      text-decoration: none;
      color:$white;
      font-family: montserrat;
      font-style: normal;
      font-weight: normal;
      padding: 10px;
  }
.menu-item:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 0;
    bottom: 0;
    left: 0;
    background-color: $white;
    visibility: hidden;
    transition: all 0.5s ease-in-out;
}

.active-item{
    font-weight: 500;
}

.menu-item:hover:before,
.active-item:before {
    visibility: visible;
    height: 3px;
}



